<script>
export default {
    
}
</script>

<template>
<div>
    <div class="sidebar">
        <router-link :to="{name:'home'}">Home</router-link>
        <router-link :to="{name:'listArticles'}">List Articles</router-link>
        <router-link :to="{name:'addArticle'}">Add Article</router-link>
        <router-link :to="{name:'addCategory'}">Add Category</router-link>
        <router-link :to="{name:'listCategories'}">List Categories</router-link>
    </div>
    <div class="content">
        <slot></slot>
    </div>
</div>
</template>

<style scoped>
.sidebar {
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #f1f1f1;
  position: fixed;
  height: 100%;
  overflow: auto;
}

.sidebar a {
  display: block;
  color: black;
  padding: 16px;
  text-decoration: none;
}
 
.sidebar a.active {
  background-color: #4CAF50;
  color: white;
}

.sidebar a:hover:not(.active) {
  background-color: #555;
  color: white;
}

div.content {
  margin-left: 200px;
  padding: 1px 16px;
  height: 1000px;
}

@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  div.content {margin-left: 0;}
}

@media screen and (max-width: 400px) {
  .sidebar a {
    float: none;
  }
}
    
</style>


